import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #CF6E36;
  height: 100vh
`;

const SiteName = styled.h1`
 font-family: KakaoBold;
 font-size: 50pt;
 color: #FAF4C0;
 margin-top: 10px;
 margin-left: 20px;
 font-weight: bold;
`;

const BlogDescription = styled.div`
  font-size: 12pt;
  font-family: KakaoRegular;
  color: #FFFFFF;
  margin-left: 20px;
  margin-top: 5px;
`;


const Schedule = styled.div`
  display: flex;
  flex-direction: row;
  margin-left: 20px;
`;

const ScheduleTitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 120px;
  margin-top: 30px;
`;


const ScheduleBox = styled.div`
  background-color: #FFFFFF;
  color: #000000;
  padding: 20px 100px;
  border-radius: 50px;
  margin: 20px 10px;
  font-family: KakaoBold;
  font-size: 10pt;
  font-weight: bold;
  margin-left: 80px;
`;

const Copybutton = styled.button`
  background-color: #FFFFFF;
  color: #000000;
  font-family: KakaoBold;
  font-size: 10pt;
  border-radius: 50px;
  padding: 20px 40px;
  margin: 20px 10px;
  font-weight: bold;
  text-shadow: 2px 2px 2px gray;
  position: absolute;
  right: 100px;
`;

const Posts = styled.div`
  display: flex;
  margin-top: 10px;
  flex-direction: column;
  background-color: #FFFFFF;
  margin-left: 100px;
  margin-right: 100px;
  margin: 20px 100px;
  border-radius: 10px;
`;

const PostBox = styled.div`
  margin-top: 30px;
  background-color: #F2F2F2;
  margin-left: 20px;
  margin: 20px 20px;
  border-radius: 10px;
  padding: 10px 20px;
`;

const PostBoxTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 10px;
  margin: 20px 20px;
`;


const PostItemBox = styled.div`
  background-color: #FFFFFF;
  margin-left: 20px;
  border-radius: 10px;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  position: relative;
  padding: 20px 20px;
  margin: 30px 20px;
`;

const PostItemimage = styled.img`
  width: 100px;
  height: 60px;
  margin-top: 20px;
  margin-left: 20px;
`;

const PostItems = styled.div`
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-top: 20px;
`;

const PostItemTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
`;

const PostItem = styled.div`
  font-family: KakaoRegular;
  font-size: 9pt;
  color: #000000;
  margin-left: 25px;
  margin-top: 10px;
`;

const SummaryItem = styled.div`
  font-family: KakaoBold;
  font-size: 10pt;
  color: #000000;
  font-weight: bold;
  position: absolute;
  bottom: 10px;
  right: 20px;
  `;

  function RouteView() {
    return (
    <Container>
        <div>
          <div>
            <SiteName>Trip Again</SiteName>
            <BlogDescription>  </BlogDescription>
          </div>
          <div>
          <ScheduleTitle>SCHEDULE</ScheduleTitle>
          <Schedule>
            <ScheduleBox>Europe 2019.07.07 ~ 2019.08.13      </ScheduleBox>
            <Copybutton> </Copybutton>
          </Schedule>
          </div>

          <Posts>
            <PostBox>
                <PostBoxTitle>1  - </PostBoxTitle>
                <PostItemBox>
                    <PostItemimage src="Incheon.jpg" alt = "Incheon"/>
                    <PostItems>
                        <PostItemTitle>  </PostItemTitle>
                        <PostItem>  ,    </PostItem>
                        <SummaryItem> / 13  / : 1,400,000</SummaryItem>
                    </PostItems>
                </PostItemBox>
                <PostItemBox>
                    <PostItemimage src="Hisro.jpg" alt = "Hisro"/>
                    <PostItems>
                        <PostItemTitle>  </PostItemTitle>
                        <PostItem>  .   50 </PostItem>
                        <SummaryItem>  / 20  / : 14,000</SummaryItem>
                    </PostItems>
                </PostItemBox>
                <PostItemBox>
                    <PostItemimage src="Hotel.jpg" alt = "Hotel"/>
                    <PostItems>
                        <PostItemTitle>  </PostItemTitle>
                        <PostItem>    </PostItem>
                        <SummaryItem>: 14,000</SummaryItem>
                    </PostItems>
                </PostItemBox>
            </PostBox>
          </Posts>
        </div>
    </Container>
      );
    }
    
    export default RouteView;

