import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #CF6E36;
`;

const SiteName = styled.h1`
 font-family: KakaoBold;
 font-size: 50pt;
 color: #FAF4C0;
 margin-top: 10px;
 margin-left: 20px;
 font-weight: bold;
`;

const BlogDescription = styled.div`
  font-size: 12pt;
  font-family: KakaoRegular;
  color: #FFFFFF;
  margin-left: 20px;
  margin-top: 5px;
`;


const Schedule = styled.div`
  display: flex;
  flex-direction: row;
  margin-left: 20px;
`;

const ScheduleTitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 120px;
  margin-top: 30px;
`;


const ScheduleBox = styled.div`
  background-color: #FFFFFF;
  color: #000000;
  padding: 20px 100px;
  border-radius: 50px;
  margin: 20px 10px;
  font-family: KakaoBold;
  font-size: 10pt;
  font-weight: bold;
  margin-left: 80px;
`;

const Copybutton = styled.button`
  background-color: #FFFFFF;
  color: #000000;
  font-family: KakaoBold;
  font-size: 10pt;
  border-radius: 50px;
  padding: 20px 40px;
  margin: 20px 10px;
  font-weight: bold;
  text-shadow: 2px 2px 2px gray;
  position: absolute;
  right: 90px;
`;

const PostTitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 120px;
  margin-top: 20px;
`;

const Posts = styled.div`
  display: flex;
  margin-top: 10px;
  flex-direction: column;
  background-color: #FFFFFF;
  margin: 20px 20px;
  margin-left: 100px;
  margin-right: 100px;
  border-radius: 10px;
  padding: 10px 10px;
`;

const PostBox = styled.div`
  margin-top: 20px;
  background-color: #F2F2F2;
  margin-left: 20px;
  border-radius: 10px;
  margin: 20px 20px;
  padding: 10px 10px;
`;

const PostBoxTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 20px;
  margin: 20px 22px;
`;

const PostItemBox = styled.div`
  background-color: #FFFFFF;
  margin-left: 20px;
  margin-top: 20px;
  margin: 20px 20px;
  border-radius: 10px;
  padding: 10px 10px;
`;

const PostItemTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 10px;
  margin: 10px 20px;
`;

const PostItem = styled.div`
  font-family: KakaoRegular;
  font-size: 10pt;
  color: #000000;
  margin-left: 20px;
  margin: 20px 22px;
`;

const PostItemImage = styled.img`
  width: 200px;
  height: 150px;
  margin-top: 20px;
  display: block;
  margin-left: auto;
  margin-right: auto;
`;

const SummaryItem = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
  margin: 20px auto;
`;

const Tags = styled.div`
  display: flex;
  margin-top: 20px;
  margin-left: 20px;
  flex-direction: row;
  margin: 10px 20px;
`;

const TagBox = styled.div`
  background-color: #1EFFF1;
  color: #000000;
  font-family: KakaoRegular;
  font-size: 10pt;
  padding: 10px 20px;
  border-radius: 50px;
  margin-left: 10px;
`;

const subs = styled.div`
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  position: absolute;
`;

const HeartButton = styled.button`
  background-color: #FFD9FA;
  font-family: KakaoRegular;
  font-size: 10pt;
  padding: 10px 20px;
  border-radius: 50px;
  margin-left: 100px;
  color: #000000;
  border: none;
`;

const HideButton = styled.button`
  background-color: #FFFFFF;
  color: #000000;
  font-size: 10pt;
  padding: 10px 20px;
  border-radius: 50px;
  position: absolute;
  right: 100px;
  border: none;
`;

const CommentBox = styled.div`
  background-color: #FEF0F9;
  border-radius: 10px;
  margin: 10px 20px;
  margin-left: 100px;
  margin-right: 100px;
  padding: 8px 10px;
  display: flex;
  flex-direction: column;
`;

const CommentContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin: 8px 10px;
`;

const Comments = styled.div`
  background-color: #FFFFFF;
  border-radius: 20px;
  padding: 10px 10px;
  margin: 8px 8px;
  display: flex;
  flex-direction: row;
  border: 2px solid black;
`;

const Profiles = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-left: 10px;
  align-items: center;
`;

const Profile = styled.img`
  weight: 70px;
  height: 70px;
  border-radius: 50%;
  margin: 10px 10px;
  border: 2px solid black;
`;

const Nickname= styled.div`
  margin: 5px 15px;
  font-family: KakaoRegular;
  font-size: 8pt;
  margin-bottom: 10px;
`;

const CommentItem = styled.div`
  color: #000000;
  font-family: KakaoRegular;
  font-size: 10pt;
  margin: 50px 20px;
`;

const HeartItem = styled.div`
  position:absolute;
  right: 100px;
  margin-right: 40px;
  margin-top: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const HeartIcon = styled.div`
  margin-top: 8px;
  margin-right: 10px;
`;

const HeartCount = styled.div`
  margin-top: 5px;
  margin-right: 10px;
  font-family: KakaoRegular;
  font-size: 9pt;
`;

function IndividualBlogPost() {
  return (
    <Container>
      <div>
        <div>
          <SiteName>Trip Again</SiteName>
          <BlogDescription>  </BlogDescription>
        </div>
        <div>

        <ScheduleTitle>SCHEDULE</ScheduleTitle>
        <Schedule>
        <ScheduleBox>Europe 2019.07.07 ~ 2019.08.13      </ScheduleBox>
        <Copybutton> </Copybutton>
        </Schedule>
        </div>
        <div>
        <PostTitle>POST</PostTitle>
        <Posts>
          <PostBox>
            <PostBoxTitle>1  - </PostBoxTitle>
            <PostItemBox>
              <PostItemTitle>   -  12,000</PostItemTitle>
              <PostItem>
                  ...       .
               3     , 8    9   .
               0 20  , 0 55  !
              </PostItem>
            <PostItemImage src = "Incheon.jpg" alt = "Incheon" />
            </PostItemBox>
            <SummaryItem> / 13  /  1,400,000</SummaryItem>
            <PostItemBox>
              <PostItemTitle>   -  14,000</PostItemTitle>
              <PostItem>
                12         .         !
              </PostItem>
            </PostItemBox>
            <Tags>
              <TagBox>#</TagBox>
              <TagBox>#</TagBox>
            </Tags>
          </PostBox>
        </Posts>
        <subs>
          <HeartButton> 102  </HeartButton>
          <HideButton> </HideButton>
        </subs>
        <CommentBox>
          <CommentContainer>
          <Comments>
            <Profiles>
            <Profile src="Profile1.jpg" alt="Profile1"/>
            <Nickname></Nickname>
            </Profiles>
            <CommentItem>  .       .</CommentItem>
            <HeartItem>
              <HeartIcon></HeartIcon>
              <HeartCount>3</HeartCount>
            </HeartItem>
          </Comments>
          <Comments>
          <Profiles>
            <Profile src="Profile2.jpg" alt="Profile2"/>
            <Nickname></Nickname>
            </Profiles>
            <CommentItem> !       </CommentItem>
            <HeartItem>
              <HeartIcon></HeartIcon>
              <HeartCount>10</HeartCount>
            </HeartItem>
          </Comments>
          <Comments>
          <Profiles>
            <Profile src="Profile3.jpg" alt="Profile3"/>
            <Nickname></Nickname>
            </Profiles>
            <CommentItem>     ! </CommentItem>
            <HeartItem>
              <HeartIcon></HeartIcon>
              <HeartCount>15</HeartCount>
            </HeartItem>
          </Comments>
          </CommentContainer>
        </CommentBox>
      </div>
      </div>
    </Container>
  );
};

export default IndividualBlogPost;

