import React from 'react';
import styled from 'styled-components';
import AddCity from '../EnrollPost/AddCity/Integrated'
import { useState } from 'react';
import AddPlace from '../EnrollPost/AddPlace/Integrated'
import AddTransport from '../EnrollPost/AddTransport/Integrated'

const Container = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #CF6E36;
  position: relative;
`;

const Categories = styled.div`
  display: flex;
  flex-direction: row;
  margin-left: 20px;
`;

const CategoryHeader = styled.div`
font-size: 12pt;
font-family: KakaoBold;
color: #FFFFFF;
margin-left: 60px;
margin-top: 25px;
`;

const CategorySelect = styled.select`
  color: #000000;
  background-color: #FFFFFF;
  padding: 5px 10px;
  border: none;
  border-radius: 50px;
  margin: 10px 0;
  margin-left: 20px;
  margin-top: 20px;
  font-family: KakaoRegular;
  font-size: 10pt;
`;

const ScheduleTitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 120px;
  margin-top: 30px;
`;

const ScheduleBox = styled.div`
  background-color: #FFFFFF;
  color: #000000;
  padding: 20px 30px;
  border-radius: 50px;
  margin: 20px 10px;
  font-family: KakaoBold;
  font-size: 10pt;
  font-weight: bold;
  margin-left: 100px;
  margin-right: 100px;
`;

const PostTitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 120px;
  margin-top: 20px;
`;

const Posts = styled.div`
  display: flex;
  margin-top: 10px;
  flex-direction: column;
  background-color: #FFFFFF;
  margin: 20px 20px;
  margin-left: 100px;
  margin-right: 100px;
  border-radius: 10px;
  align-items: center;
`;

const PostBox = styled.div`
  background-color: #F2F2F2;
  margin-left: 20px;
  margin: 20px 20px;
  margin-top: 30px;
  border-radius: 10px;
  position: relative;
  padding: 10px 20px;
`;

const PostBoxTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 10px;
  margin: 20px 20px;
`;

const SaveButton = styled.button`
  background-color: #FBFB49;
  color: #000000;
  border-radius: 50px;
  padding: 10px 20px;
  position: absolute;
  top: 22px;
  right: 40px;
  z-index: 1;
  font-family: KakaoBold;
  font-size: 10pt;
  font-weight: bold;
  border: none;
`;

const PostItemBox = styled.div`
  background-color: #FFFFFF;
  margin-left: 20px;
  margin-top: 20px;
  margin: 30px 20px;
  border-radius: 10px;
  padding: 20px 20px;
`;

const PostItemTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 10px;
  margin: 10px 20px;
`;

const PostItem = styled.div`
  font-family: KakaoRegular;
  font-size: 10pt;
  color: #000000;
  margin-left: 20px;
  margin: 20px 22px;
`;

const AddImageBox = styled.div`
  border: 1px dashed #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 20px;
`;

const AddImage = styled.img`
  width: 200px;
  height: 200px;
  margin-top: 20px;
  display: block;
  margin: 50px 50px;
  margin-left: auto;
  margin-right: auto;
`;

const Manages = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 15px;
`;

const TagButton = styled.button`
  background-color: #1EFFF1;
  color: #000000;
  font-family: KakaoBold;
  font-weight: bold;
  font-size: 10pt;
  padding: 10px 20px;
  border-radius: 50px;
  position: absolute;
  left: 20px;
  margin-left: 20px;
  border: none;
`;

const ManageButton = styled.button`
  background-color: #FBFB49;
  color: #000000;
  font-family: KakaoBold;
  font-weight: bold;
  font-size: 10pt;
  padding: 10px 20px;
  border: none;
  border-radius: 50px;
  margin-left: 10px;
`;

const AddButton = styled.button`
  background-color: #F9B507;
  color: black;
  padding: 20px 30px;
  justify-content: center;
  border-radius: 50px;
  border: none;
  align-items: center;
  margin-bottom: 20px;
  font-family: KakaoBold;
  font-size: 10pt;
`

function ManagePost() {

  const [click, setClick] = useState(false);
  const [click2, setClick2] = useState(false);
  const [click3, setClick3] = useState(false);

  const closeWindow = (val) => {

    setClick(val)


  }

  const closeWindow2 = (val) => {

    setClick2(val)


  }
  const closeWindow3 = (val) => {

    setClick3(val)
  }

  return (
    <Container>
      <Categories>
        <CategoryHeader>CATEGORY</CategoryHeader>
        <CategorySelect>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option></option>
          <option>/</option>
        </CategorySelect>
      </Categories>
      <ScheduleTitle>SCHEDULE</ScheduleTitle>
      <ScheduleBox>Europe 2019.07.07 ~ 2019.08.13      </ScheduleBox>
      <PostTitle>POST</PostTitle>
      <Posts>
        <PostBox>
          <PostBoxTitle>1  - </PostBoxTitle>
          <SaveButton></SaveButton>
          <PostItemBox>

            <PostItem>
                  ...       .
               3     , 8    9   .
               0 20  , 0 55  !
            </PostItem>
            <AddImageBox>
              <AddImage src="AddImg.jpg" alt="AddImg" />
            </AddImageBox>
          </PostItemBox>
          <Manages>
            <TagButton># </TagButton>
            <ManageButton onClick={() => {

              setClick3(true)
              setClick(false)
              setClick2(false)

            }}> </ManageButton>
            <ManageButton onClick={() => {
              setClick2(true)
              setClick(false)
              setClick3(false)
            }}> </ManageButton>

          </Manages>
        </PostBox>
        <AddButton onClick={() => {

          setClick(true)
          setClick2(false)
          setClick3(false)

        }}> </AddButton>
        {click && <AddCity closeWindow={closeWindow} />}
        {click2 && <AddPlace closeWindow2={closeWindow2} />}
        {click3 && <AddTransport closeWindow3={closeWindow3}/>}
      </Posts>
    </Container>
  );
}

export default ManagePost;
